

body{
    background: #000;
    height: 100vh;
}


.App {
    text-align: center;
    padding: 5%;
    color: #fff;
    font-size:24px;
}

.App-title {
    font-family: 'Courgette', cursive;
    font-size: 4.5em;
    color: #fff;  
}

.sound-wave {
    width:120px;
    margin-bottom: 20px;
    padding-left:15px;
}

.artist-output {
    background:url("./images/the-speaker-grill-2184439_1920.jpg");
    background-size: cover;
   margin-top: 30px;
   border: 5px solid #3cc94b;  
   -webkit-box-shadow: 0px 3px 20px 3px rgba(144, 142, 142, 0.6);  
           box-shadow: 0px 3px 20px 3px rgba(144, 142, 142, 0.6);
}

/*Profile Styles*/
.profile {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;   
}

.profile-info {
    margin-left: 10px;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.profile-img {
    width: 150px;
    height: 150px;
    margin-top: 20px;
    border: 3px solid #fff;
    border-radius: 75px;
    -o-object-fit: cover;
       object-fit: cover;
}

.profile-name {
    font-size: 32px;
}

.profile-followers, .profile-genres {
    font-size: 24px;
}

/* Gallery Styles */
.tracks-display {
    margin-top: 20px;    
}

.track {
display: inline-block;
width: 220px;
height: 220px;
margin: 10px;
text-align: left;
cursor: pointer;
position: relative;
}

.track-img {
width: 220px;
height: 220px;
border: 3px solid #fff;
border-radius: 3px;
-o-object-fit: cover;
   object-fit: cover;
position: absolute;
}

.track-text {
background-color: #000;
color: #fff;
font-size: 14px;
opacity: 0.75;
width: 214px;
margin-left: 3px;
text-align: center;
position: absolute;
bottom: 0;
padding: 5px;
}

.track-play {
    position: absolute;
    width: 220px;
    height: 220px;
}

.track-play-inner {
    position: absolute;
    text-align: center;
    font-size: 25px;
    background-color: #000;
    border-radius: 30px;
    width: 60px;
    height: 60px;
    color: #fff;
    padding-top: 13px;
    margin-left: 80px;
    margin-top: 80px;
    opacity: 0;
}

.track-play-inner:hover {
    opacity: 0.8;
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
}

@-webkit-keyframes fade-in {
    from {
        opacity: 0;
    }

    to {
        opacity: 0.8;
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
    }

    to {
        opacity: 0.8;
    }
}



